.about {
  --opacity: 0;
  display: grid;
  align-items: center;
  width: 100%;
  height: 100vh;
  position: relative;
  &::after {
    opacity: var(--opacity);
    position: absolute;
    top: 0;
    z-index: -1;
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-image: url("../images/bg.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
  }
  &Wrap {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 40%;
    height: 80%;
    color: whitesmoke;
    .who {
      font-family: "Boska-light";
      mix-blend-mode: difference;
      font-size: 2.3vmax;
      text-align: right;
    }
    .text {
      font-family: "Trispace", sans-serif;
      font-weight: 100;
      // font-variant: small-caps;
      mix-blend-mode: difference;
      font-size: 1.5vmax;
      text-align: right;
    }
    .love__wrap,
    .iam__wrap {
      span {
        display: inline-block;
      }
      .cursor {
        display: inline-block;
        background-color: #f2f2f2;
        margin-left: 0.1rem;
        width: 2px;
        animation: blink 1s infinite;
        &.typing {
          animation: none;
        }
      }
    }
    .typing__love,
    .typing__iam {
      color: #7a7a7a;
    }
  }
  h1 {
    color: #fff;
    mix-blend-mode: difference;
    position: absolute;
    top: 45%;
    transform: translateY(-50%);
    right: 5%;
    font-family: "Boska-light";
    font-size: 14vmax;
    letter-spacing: 0.03em;
    line-height: 2;
  }
}

@keyframes blink {
  0% {
    background-color: #f2f2f2;
  }
  49% {
    background-color: #f2f2f2;
  }
  50% {
    background-color: transparent;
  }
  99% {
    background-color: transparent;
  }
  100% {
    background-color: #f2f2f2;
  }
}
