.contact {
  margin: auto;
  width: 100%;
  overflow: hidden;
  height: 100vh;
  font-family: "Boska-Bold";
  position: relative;
  h2 {
    left: 50%;
    top: 30%;
    transform: translate(-50%, -50%);
    position: absolute;
    overflow: hidden;
    font-size: 12vmax;
    color: #d8d8d8;
    line-height: 2;
    mix-blend-mode: difference;
  }
  .contact__text {
    width: 100%;
    height: 10%;
    text-align: center;
    position: absolute;
    font-size: 4vmax;
    bottom: 20%;
    mix-blend-mode: difference;
    color: #d8d8d8;
    overflow: hidden;
    .contacts {
      a {
        color: #d8d8d8;
      }
      width: fit-content;
      position: relative;
      --Width: 0;
      &::after {
        position: absolute;
        bottom: -5%;
        left: 0;
        content: "";
        display: block;
        height: 3px;
        width: var(--Width);
        background-color: #d8d8d8;
      }
    }
    .dot {
      opacity: 0;
    }
  }

  &Bg {
    z-index: -1;
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    background: url("../images/bg.svg") no-repeat;
    background-size: 80%;
    background-position: center 25%;
  }
}
