nav {
  font-family: "Epilogue", sans-serif;
  font-weight: 400;
  width: 5%;
  height: 100%;
  position: fixed;
  z-index: 300;
  right: 0;
  top: 0;
  background: transparent;
  .menu {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    li {
      writing-mode: vertical-lr;
      cursor: pointer;
      font-size: 1.8vmax;
      span {
        color: rgb(0, 0, 0);
      }
    }
  }

  .menu li span.active {
    opacity: 1;
  }
}
