@import url("https://fonts.googleapis.com/css2?family=Epilogue:wght@100;200;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Trispace:wdth,wght@87.5,100;87.5,200;87.5,300;87.5,500&display=swap");

html::-webkit-scrollbar {
  display: none;
}
body {
  height: 100%;
  width: 100%;
  background-color: #f2f2f2;
}
.circle {
  z-index: 1000;
  color: red;
  width: 30px;
  height: 30px;
  position: fixed;
  transform: translate(-50%, -50%);
  top: 0;
  left: 0;
  opacity: 0.3;
}
