.work {
  width: 100%;
  display: block;
  position: relative;
  &Wrap {
    padding-top: 10vmax;
    margin: auto;
    height: 100%;
    width: 95%;
    justify-items: center;
    > div {
      position: relative;
      padding: 5vmax 0;
      width: 100%;
      height: 40vmax;
      display: flex;
      img {
        width: 95%;
        object-fit: contain;
      }
      .info {
        width: 80%;
        height: 100%;
        &__text {
          margin: auto;
          width: 80%;
          h2 {
            text-align: center;
            font-family: "Epilogue", sans-serif;
            font-weight: 500;
            font-size: 1.8rem;
            line-height: 3;
            position: relative;
            &:hover {
              &::after {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                content: "";
                display: block;
                height: 50%;
                width: 30%;
                background-color: rgba(63, 63, 63, 0.13);
              }
            }
          }
          p {
            overflow: hidden;
            line-height: 1.3;
            font-family: "Trispace", sans-serif;
            font-weight: 200;
          }
        }
      }
      .img {
        position: absolute;
        width: 40%;
        height: fit-content;
      }
    }
    .taco {
      .info {
        width: 75%;
      }
      .img {
        bottom: -18%;
        right: 0;
      }
    }
    .zzan {
      justify-content: flex-end;
      margin-top: 10%;
      .info {
        width: 70%;
        img {
          width: 100%;
        }
        &__text {
          width: 28%;
          position: absolute;
          top: 10%;
          left: 0;
          h2 {
            text-align: left;
            &:hover {
              &::after {
                position: absolute;
                top: 50%;
                left: 0%;
                transform: translate(-20%, -50%);
                content: "";
                display: block;
                height: 50%;
                width: 30%;
                background-color: rgba(63, 63, 63, 0.13);
              }
            }
          }
        }
      }
      .img {
        left: 0;
        bottom: -20%;
        transform-origin: center;
        svg {
          width: 100%;
          height: 100%;
        }
      }
    }
    .chair {
      justify-content: center;
      .info {
        width: 80%;
        img {
          width: 100%;
        }
      }
      .img {
        width: 30%;
        right: 0;
        bottom: -29%;
        z-index: 5;
        outline: 10px solid #f2f2f2;
      }
    }
    .cloneCoding {
      position: relative;
      display: flex;
      flex-direction: column;
      margin-top: 20%;
      h3 {
        font-family: "Epilogue", sans-serif;
        font-size: 3vmax;
        font-weight: 500;
        line-height: 2.5;
        overflow: hidden;
      }
      .cloneList {
        width: 20%;
        margin-left: 3%;
        font-size: 2vmax;
        p {
          width: fit-content;
          position: relative;
          line-height: 2;
          cursor: pointer;
          font-family: "Epilogue", sans-serif;
          font-weight: 300;
          color: rgb(105, 105, 105);
          overflow: hidden;
          --Width: 0;
          b {
            font-family: "Epilogue", sans-serif;
            font-size: 1vmax;
          }
          &::after {
            position: absolute;
            bottom: 5%;
            left: -10%;
            display: block;
            content: "";
            width: var(--Width);
            height: 90%;
            background-color: rgba(105, 105, 105, 0.253);
          }
        }
      }
      .cloneSites {
        overflow: hidden;
        left: 40%;
        position: absolute;
        width: 70%;
        height: 80%;
        bottom: 0;
        > div {
          position: absolute;
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: center;
          a {
            width: 100%;
            height: 90%;
            overflow: hidden;
            img {
              width: 100%;
            }
          }
          .cloneInfo {
            width: 100%;
            height: 100%;
            line-height: 2;
            writing-mode: vertical-lr;
            font-family: "Trispace", sans-serif;
            font-weight: 200;
            font-size: 1.5vmax;
            text-align: center;
            span {
              color: red;
            }
          }
        }
        #apple {
          img {
            object-fit: contain;
          }
          .cloneInfo {
            text-align: left;
          }
        }
      }
    }
  }
  &Bg {
    opacity: 0.4;
    z-index: -1;
    background: url("../images/circle.png") no-repeat,
      url("../images/circle2.png") no-repeat;
    background-position: 120% -20%, 150% -20%;
    background-size: 80%, 80%;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 20%;
    animation: bgani 10s infinite linear;
  }
  @keyframes bgani {
    0% {
      opacity: 0.4;
    }
    30% {
      opacity: 0.7;
    }
    50% {
      opacity: 0.5;
    }
    70% {
      opacity: 0;
    }
    100% {
      opacity: 0.4;
    }
  }
}
