.mouse {
  pointer-events: none;
  z-index: 100;
  position: fixed;
  mix-blend-mode: difference;
  top: -1%;
  width: 2em;
  height: 2em;
  background-color: rgb(255, 255, 255);
  color: rgb(255, 255, 255);
  border-radius: 50%;
  display: grid;
  place-items: center;
  font-weight: bold;
  font-size: 1.2vmax;
}
